  width: 100%;

}

.showDrawerContainer .textProduct {
  color: whitesmoke;
  font-size: 0.7em;
}
.drawerProductImg{
  width:200px!important
}
.footerShoppingCart{
  font-size: 0.8em;

}

.footerShoppingCart button{
  width:80px;
  font-size: 1em;
  height: 7vh;
  display: flex;
  align-items: center;
  text-decoration: none;
  justify-content: center;
  background-color:rgba(6, 134, 200, 0.863)
}
.footerShoppingCart button:hover{
  width:10vw;
  height: 7vh;
  display: flex;
  align-items: center;
  text-decoration: none;
  justify-content: center;
  background-color:rgb(235, 145, 43)
}

