.rs-drawer-content{ /* panel del drawer */
    background-color: rgba(0, 0, 0, 0.746) !important;
}

.rs-btn-primary{ /* boton primario */
   background-color: rgba(20, 149, 172, 0.788) !important;
}
.rs-btn-subtle{ /* boton secundario */
    color: black;
}
.rs-drawer-title{ /* titulo drower */
    text-align: center;
    font-size: 2vw !important;
    margin-bottom: -1vh !important;
}
.rs-drawer-body::-webkit-scrollbar{
    width: 10px;
}
.rs-drawer-body::-webkit-scrollbar-track{
    background: #a3a3a3;  
    border-radius: 5px;
  }
  .rs-drawer-body::-webkit-scrollbar-thumb{
    background: rgb(88, 88, 88);
    border-radius: 5px;
  }
  
  .rs-drawer-body::-webkit-scrollbar-thumb:hover {
    background: rgb(78, 114, 119);
  }
.drawerProductContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: grey;
    color: white;
    margin: 2vh;
    padding-left: 1vw;
    border-radius: 0.8vh;
    box-shadow: 3px 2px 4px 1px rgba(0, 0, 0, 0.5);
}

.textProduct{
    color: whitesmoke;
    font-size: 1vw;
}

.drawerProductImg{
    background-size: cover;
    background-position: center;
    height: 20vh;
    border-top-right-radius: 0.8vh;
    border-bottom-right-radius: 0.8vh;
    display: flex;
    justify-content: flex-end;
}

.removeDrawerProduct{
    color: rgba(0, 0, 0, 0.795);
    width: 1.5vw;
    height: 3vh;
}

/*------------- CategoryDrawer ------------*/

.navLinksCategory{
    display:flex;
    align-items: center;
    padding: 2vh;
    margin: 1vh 1vh;
    border-radius: 8px;
    box-shadow: -6px -6px 18px rgba(112, 112, 112, 0.342);
    transition: all ease 0.3s;
    transform: scale(0.9);
    background-color: rgba(0, 0, 0, 0.911);
    border: 1px solid #1675e0;
}
.navLinksCategory:hover{
    transform: scale(1);
}
.textLinksCategory{
    color: rgb(255, 255, 255);
    font-size: 1.5em;
    padding: 1vh;
    margin-left: 1vw;
}

.drawerCategoryImg{
    min-width: 50px;
    min-height: 50px;
    width: 4vw;
    height: 4vw;
    border-radius: 50%;
}

/*Manage Product*/
.manageProduct{
    max-width: 95%;
    min-height: 3.5vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 2vh;
}
.manageQuantityProduct{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.manageQuantityProduct span{
    margin: 0 .5vw;
}
.manageQuantityProduct span .rs-input{
    font-size: 16px;
    font-weight: bold;
    color: green;
    text-align: center;
    max-width: 4vw;
    max-height: 3vh;
}
.bottonManage{
    cursor:pointer;
    font-size: 16px;
}
.bottonManage.edit{
    color: #E0CD22;
}
.bottonManage.delete{
    color: #d62915;
}

.rs-drawer-header h4 {
    color: whitesmoke !important;
}
@media only screen and (max-width:600px) {
    .rs-drawer-left.rs-drawer-sm, .rs-drawer-right.rs-drawer-sm {
      width: 95% !important;
    }
    
    .showDrawerContainer .rs-drawer-content{ /* panel del drawer */
      background: rgba(0, 0, 0, 0.561);
      box-shadow: 5px 5px 80px rgba(182, 182, 182, 0.76);
      width: 100%;
    
    }
    
    .showDrawerContainer .textProduct {
      color: whitesmoke;
      font-size: 0.7em;
    }
    .drawerProductImg{
      width:200px!important
    }
    .footerShoppingCart{
      font-size: 0.8em;
    
    }
    
    .footerShoppingCart button{
      width:80px;
      font-size: 1em;
      height: 7vh;
      display: flex;
      align-items: center;
      text-decoration: none;
      justify-content: center;
      background-color:rgba(6, 134, 200, 0.863)
    }
    .footerShoppingCart button:hover{
      width:10vw;
      height: 7vh;
      display: flex;
      align-items: center;
      text-decoration: none;
      justify-content: center;
      background-color:rgb(235, 145, 43)
    }
    .rs-input{
        padding:2px
    }
    #input604a11344582ae0015f203d7{
     color:black!important;
     padding:2px!important;
      font-size: 0.5em!important;
    }
    .manageQuantityProduct{
      width:70%;
      color:black
    }
