*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/*ESTILOS SIGN IN AND SIGN UP*/
.backBtnSignIn{
  width: 7vw;
  background-color: #1572b4;
  color: whitesmoke;
  margin: 0.2vw;
  box-shadow: 5px 5px 20px rgba(6, 135, 200, 0.801);
  border: 2px solid rgba(6, 135, 200, 0.801);
  position: fixed;
}

.registro, .registroForm{
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 4vh;
  color: rgb(250, 250, 250);
  min-height: 100vh;
  justify-content: flex-start;
  border-right: solid 2px rgb(65, 166, 216);
  }
  .registro {
    padding-right: 5vw;
  }
.formulario, .formularioSignIn {
  display: flex;
  padding: 4vh ;
  align-items: center;
  flex-direction: column;
  background-color: rgb(16, 16, 16);
  width: 40vw;
  background: transparent;
}
.formularioSignIn {
  margin-bottom: 22.5vh;
  margin-right: 1vw;
}
.formulario{
  margin-right: 6vw;
  /* border-left: solid 2px rgb(65, 166, 216); */
  /* margin-top: 5vh; */
}
.formulario h2, .formularioSignIn h2{
  margin-bottom: 1vw ;
  padding-left: 3vw;
  padding-right: 23vw;
  border-bottom: solid 2px rgb(65, 166, 216);
  margin-right: 1vw;
  }
.formulario h4{
  margin-bottom: 4vh;
  text-align: center;
  }

.inputDiv{
  width: 100%;
  position: relative;
 
}
.inputDiv select{
  margin-bottom: 3vh;
}
input[type=file]{
  background-color: #fafafa15;  
} 
.formulario input,.formularioSignIn input,.formulario select{
  /* border: none; */
  outline: none;
  width: 100%;
  padding: 1.5vh 2vh ;
  border-radius: 8px;
  margin-bottom: 3vh;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.356);
  color: rgb(56, 56, 56)!important;
  border: solid 2px rgba(6, 134, 200, 0.863);
  transition: all ease .3s;
  box-shadow: 5px 5px 12px rgba(113, 234, 255, 0.507);
  background-color: white;
}
.formulario input:hover, .formularioSignIn input:hover{
  border: none;
  outline: none;
  width: 100%;
  padding: 1.5vh 2vh ;
  border: solid 2px rgb(255, 123, 0);
  border-radius: 25px;
  margin-bottom: 3vh;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.356);
  color: rgb(56, 56, 56)!important;
  box-shadow: 5px 5px 12px rgba(255, 213, 77, 0.507);
  }
  .formulario select:hover {
    border: none;
    outline: none;
    width: 100%;
    padding: 1.5vh 2vh ;
    border: solid 2px rgb(255, 123, 0) !important;
    border-radius: 25px;
    margin-bottom: 3vh;
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.356);
    color: rgb(56, 56, 56)!important;
    box-shadow: 5px 5px 12px rgba(255, 213, 77, 0.507);
}
.eye{
  position: absolute;
  top: 16%;
  right: 2%;
  color:  rgb(56, 56, 56);
  font-size: 1.4rem;
  
}
.enviar {
  width: 15vw;
  margin-bottom: 3vh;
  border-radius: 25px;
  transition: all 0.2s;
  padding:15px !important;
  background-color:rgba(6, 134, 200, 0.863) ;
  color:rgb(250, 250, 250);
  font-weight: bold;
  border: solid 2px rgba(6, 134, 200, 0.863);
  box-shadow: 5px 5px 12px rgba(113, 234, 255, 0.507);
}
.enviar:hover{
  transform: scale(1.06);
  background-color:#00000077;
  border: solid 2px rgb(255, 123, 0);
  color:rgb(255, 123, 0);
  box-shadow: 5px 5px 12px rgba(255, 213, 77, 0.507);
  }
.enviar:active{
  /* border: none; */
  transform: translateY(7px);
  box-shadow: 0px 0px rgba(0, 0, 0, 0.555);
 
}
button:focus {
  outline: none;
}
.navBarLinks {
  text-decoration: none;
  font-size: 25px;
  color: rgba(6, 135, 200, 0.801);
}

.containerRegisterUser {
display: flex;
justify-content: space-between;
align-items: flex-start;
background-image: url(../assets/fondo.jpeg);
}

.text{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
margin-bottom: 4vh;
}
/*RESPONSIVE*/
@media only screen and (max-width:600px) {
  .containerRegisterUser {
    flex-wrap: wrap;
    }
 
     .registro, .registroForm{
       border:none;
       width: 100%;
      padding: 0vh;
      color: rgb(250, 250, 250);
      min-height: 0vh;
     
       }
      .registro {
       
        padding-right: 5vw;
      }
      
      .formulario, .formularioSignIn {
        display: flex;
        /* padding: 4vh ; */
        width: 90%;
        margin:auto;
       

      }
      .formularioSignIn {
        margin-bottom: 0vh;
        margin-right: 1vw;
      }
      .formulario{
        margin-right: 6vw;
        border-left: none;
        margin-top: 1vh;
      }
      .formulario h2, .formularioSignIn h2{
       text-align: center;
        border-bottom: solid 2px rgb(65, 166, 216);
        margin:auto;
        padding:1%
        }
      .formulario h4,.formularioSignIn h4{
        text-align: center;
        font-size: 1.2em;
        }
}